#image{
    width: 100%;
    height: 60vh;
    object-fit: contain;
}

#image-cover{
    width: 100%;
    height: 60vh;  
    overflow: hidden;
}

#btn{
    background: rgb(255, 255, 255);
    border: none;
    padding: 5px 15px;
    color: #000;
    border-radius: 18px;
    margin-top: 15px;
    cursor: pointer;
    box-shadow: 0 0 2px rgb(85, 84, 84);
}

#btn:hover{
    background: rgb(2, 196, 255);
    box-shadow: 0 0 15px rgb(2, 196, 255);
    padding: 5px 17px;
    
    color: #fff;
}

.image-section{
    background: white;
    border-radius: 2px;
    box-shadow: 0 0 2px rgb(196, 194, 194);
    padding: 8px;
}

.color-section{
    background: white;
    max-height: 80vh;
    border-radius: 2px;
    box-shadow: 0 0 2px rgb(196, 194, 194);
    overflow:auto;
    padding-top: 8px;
    padding-left: 10px;
    
}